@use '../../App.scss' as *;

.home_container {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}
.home_content {
  margin: 0.5em;
  display: flex;
  flex-direction: column;
  gap: 2em;
}

.custom_carousel {
}

.carousel .slide img {
  border-radius: 7px !important;
  height: 450px;
  aspect-ratio: 16/9;
  object-fit: cover;
}

.carousel-status {
  display: none;
}

.carousel.carousel-slider .control-arrow {
  padding: 15px;
}

.thumbs-wrapper {
  display: none;
}

.bedrooms {
  width: 100%;
  margin: 1em 0 3em 0;
  .bedroom {
    display: flex;
    gap: 0.05em;
    justify-content: space-around;

    .bed_card {
      cursor: pointer;
      display: flex;
      flex-direction: column;
      align-items: center;
      box-shadow: 0px 0px 38px rgba(128, 128, 128, 0.295);
      border-radius: 7px;
      transition: all ease 0.3s;

      &:hover {
        box-shadow: 0px 0px 70px rgba(46, 46, 46, 0.295) !important;
      }

      > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        aspect-ratio: 16/9;
        border-radius: 7px;
      }
    }
  }
}

.container_activities {
  > h2 {
    margin-top: 0;
  }
  background-color: $bdp_light_green;
  padding: 1em;
  border-radius: 7px;
  .activities {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5em;
    text-align: center;

    .activity {
      cursor: pointer;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      border-radius: 7px;

      h3 {
        font-size: 1.5em;
        margin: 0;
      }

      .activity_content {
        position: absolute;
        color: white;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px;
        opacity: 0;
        transition: opacity 0.3s ease;
        backdrop-filter: blur(4px);
      }

      &:hover .activity_content {
        opacity: 1;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        aspect-ratio: 16/9;
        border-radius: 7px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .container_activities {
    .activities {
      grid-template-columns: repeat(1, 1fr) !important;
    }
  }
}

@media screen and (max-width: 930px) {
  .bedrooms {
    display: flex;
    flex-direction: column;
    align-items: center;
    .bedroom {
      flex-direction: column;
      gap: 1em;
    }
  }
}
